    import_Recommend: function import_Recommend(item) {
      var _this13 = this;

      if (!this.$store.state.userInfo) {
        this.importProduct = {
          id: item.id,
          name: item.name
        };
        this.centerDialogVisible = true;
        return;
      }

      this.$apiCall("api.Product.addFromVendorShopProduct", {
        id: item.id
      }, function (r) {
        if (r.ErrorCode == 9999) {
          item.hasImport = 1;
          item.imports++;

          _this13.$message.success("Import Success !");

          _this13.$router.push({
            name: 'productDetail',
            query: {
              id: item.id,
              name: item.name
            }
          });
        } else {
          _this13.$message.error(r.Message);
        }
      });
    },
    goToDetail: function goToDetail(item) {
      window.open("/item/".concat(item.id, "/").concat(item.name.replace(/\s+/g, "-").replace(/[^\w]/g, '_'), ".html"));
      document.title = "".concat(item.name.replace('.html', ''), " How to find :: ").concat(this.$root.$children[0].pName.b, " App - ").concat(this.$root.$children[0].pName.a, ".com");
    },
    import_pro: function import_pro() {
      var _this14 = this;

      if (!this.$store.state.userInfo) {
        this.importProduct = this.$route.params;
        this.$root.$children[0].openDialogLogin();
        return;
      }

      this.$apiCall("api.Product.addFromVendorShopProduct", {
        id: this.products.id
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this14.products.imports++;
          _this14.isAdd = 1;
          _this14.$root.$children[0].importNum++;

          _this14.$message.success("Import Success !");
        } else {
          _this14.$message.error(r.Message);
        }
      });
    }
